.wrapper{
    margin-top: 50px;
    background-color:rgba(255, 255, 255, 0.2);
    border-radius: 20px;
    margin-bottom: 50px;
    width: 90%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

@media(min-width:770px){
    .wrapper{
        width: 65%;
    }
}

.title{
    text-transform: uppercase;
    letter-spacing: 10px;
    padding-inline: 30px;
    padding-block: 15px;
    text-align: center;
    border-bottom: 1px solid black;
}

.text{
    font-size: 16px;
    margin: 20px;
    text-align: center;
}

.btn{
    border-radius: 20px;
    background: linear-gradient(to right, blue, pink);
    margin: 20px;
    border-style: none;
    font-size: 16px;
    padding: 7px;
    cursor: pointer;
}
